@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
html {
   box-sizing: border-box;
   font-size: 16px;
 }
 
 *, *:before, *:after {
   box-sizing: border-box;
 }
 
 body, h1, h2, h3, h4, h5, h6, p, ol, ul, li {
   margin: 0;
   padding: 0;
   font-weight: normal;
 }
 
 ol, ul {
   list-style: none;
 }
 
 img {
   max-width: 100%;
   height: auto;
 }

.app {
	font-family: "Poppins";
	font-weight: 400;
	height: 100vh;
	background-image: url(/country_quiz/static/media/background.41dbc550.png);
	background-size: cover;
	background-position: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

h1 {
	font-family: Poppins;
	font-style: normal;
	font-weight: bold;
	font-size: 36px;
	color: #f2f2f2;
	text-transform: uppercase;
	margin-bottom: 16px;
}

button {
	border-radius: 12px;
	height: 56px;
	width: 136px;
	font-family: Poppins;
	font-weight: 400;
	font-size: 18px;
	line-height: 27px;
	cursor: pointer;
}

.loading-text {
	color: #f2f2f2;
	font-weight: bold;
	font-size: 24px;
	line-height: 36px;
	text-transform: uppercase;
	letter-spacing: 2px;
}

.correct {
	background-color: green;
}

.incorrect {
	background-color: red;
}

.loading-text {
	width: -webkit-min-content;
	width: -moz-min-content;
	width: min-content;
	margin: 0 auto;
}

.card {
	background: #ffffff;
	border-radius: 24px;
   padding: 32px;
	padding-top: 49px;
	position: relative;
}

.quiz {
	width: 380px;
}

.quiz__flag {
	border: 1px solid black;
	width: 33%;
}

.quiz__question {
	font-weight: bold;
	font-size: 24px;
	line-height: 36px;
	color: #2f527b;
	margin-top: 12px;
	margin-bottom: 32px;
}

.quiz-options {
	margin-bottom: 42px;
}

.quiz-options__item {
	color: rgba(96, 102, 208, 0.8);
	padding: 14px 16px;
	background: #fff;
	border: 2px solid rgba(96, 102, 208, 0.7);
	border-radius: 12px;
	margin-bottom: 12px;
	display: flex;
}

.quiz-options__item:last-child {
	margin-bottom: 0px;
}

.quiz-options__item span {
	margin-right: 24px;
}

.quiz-options__item:hover {
	cursor: pointer;
	color: #ffffff;
	background: #f9a826;
	border: 2px solid #f9a826;
}

.quiz-options__item.correct {
	background: #60bf88;
	border: 2px solid #60bf88;
	color: #fff;
}
.quiz-options__item.correct:hover {
	background: #60bf88;
	border: 2px solid #60bf88;
	color: #fff;
}

.quiz-options__item.incorrect {
	background: #ea8282;
	border: 2px solid #ea8282;
	color: #fff;
}

.quiz-options__item.incorrect:hover {
	background: #ea8282;
	border: 2px solid #ea8282;
	color: #fff;
}

.quiz__traveler {
	position: absolute;
	top: -67px;
	right: 0px;
}

.traveler {
	position: absolute;
	top: -67px;
	right: 0px;
}

.quiz__btn {
	border: none;
	color: #fff;
	background-color: #f9a826;
	font-weight: bold;
	display: block;
	margin-left: auto;
}

.quiz__btn:hover {
	background-color: #d89f44;
}

.quiz__btn.disable:hover {
	background-color: #f9a826;
}

.quiz__btn.disable {
	opacity: 0.4;
}

.quiz-options__icon {
	margin-left: auto;
}

.results {
	text-align: center;
	width: 320px;
}

.results__btn {
	background-color: transparent;
	border: none;
	border: 2px solid #1d355d;
}

.results__btn:hover {
	color: #fff;
	background: #f9a826;
	border: 2px solid #f9a826;
}

.results__correct,
.results__questions {
	font-weight: 700;
}

.results__correct {
   color: #60bf88;
}

.results h2 {
	font-family: Poppins;
	font-style: normal;
	font-weight: bold;
	font-size: 48px;
	line-height: 72px;
	color: #1d355d;
}

.results__content {
	margin: 70px 0;
}


